@import "../../global.scss";
.contact {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
  }
  // Give each container a flex of 1 to split the container into 2 halves
  .left {
    flex: 1;
    overflow: hidden;
    img {
      height: 100%;
      @include mobile {
        height: 100%;
        width: 100%;
      }
    }
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include mobile {
      height: 100%;
      width: 100%;
    }

    h2 {
      font-size: 30px;
    }
    form {
      width: 70%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      @include mobile {
        width: 95%;
        height: 75%;
      }

      input {
        width: 90%;
        height: 30px;
        font-size: 14px;
      }

      textarea {
        width: 90%;
        height: 200px;
        font-size: 14px;
      }

      button {
        width: 150px;
        height: 30px;
        color: white;
        background-color: salmon;
        border-radius: 10px;
        border: none;
        font-weight: 500;
        cursor: pointer;

        &:focus {
          outline: none;
        }
      }

      span {
        color: salmon;
      }
    }
  }
}
